.popUpContainer {
    height: 55vh 
}
.popUpHeader { 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 5vh; 
    border-bottom: 3px solid gray ;
}
#popUpHeader{
    font-size: 3vh;
    color: rgba(0, 0, 0, 0.87)
}
.popUpCenter {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    height: 40vh;
    /* background-color: aqua */
}
.popUpBot {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    height: 10vh;
    border-top: 3px solid gray ;
} 
.popUpButton {
    width: 8vw;
    margin-right: 20px;
    background-color: rgb(68, 157, 68);
}
.textFieldPopUp {
    height: 10vh;
    width: 20vw
}


